import axiosInstance from "@/api/axios/axiosInstance";
import { toast } from "sonner";

// Types
export interface OutboundCall {
  id: string;
  assistantId: string;
  phoneNumber: string;
  status: "queued" | "ringing" | "in-progress" | "forwarding" | "ended";
  startedAt?: string;
  endedAt?: string;
  duration?: number;
  cost?: number;
  endedReason?: string;
  transcript?: string;
  recording?: string;
  summary?: string;
  metadata?: Record<string, any>;
  createdAt: string;
  updatedAt: string;
}

export interface OutboundCallConfig {
  assistantId: string;
  phoneNumber: string;
  phoneNumberId?: string;
  metadata?: Record<string, any>;
  maxDurationSeconds?: number;
  firstMessage?: string;
  voicemailMessage?: string;
}

export interface CallStats {
  totalCalls: number;
  successfulCalls: number;
  failedCalls: number;
  averageDuration: number;
  totalCost: number;
  callsByStatus: {
    status: string;
    count: number;
  }[];
  callsByHour: {
    hour: number;
    count: number;
  }[];
  callsByDay: {
    date: string;
    count: number;
  }[];
}

// Create outbound call
export async function createOutboundCall(data: OutboundCallConfig) {
  try {
    const res = await axiosInstance.post("/api/outboundcall/createCall", {
      id: data.assistantId,
      phoneNumber: data.phoneNumber,
      phoneNumberId: data.phoneNumberId,
      metadata: data.metadata,
      maxDurationSeconds: data.maxDurationSeconds,
      firstMessage: data.firstMessage,
      voicemailMessage: data.voicemailMessage,
    });
    toast.success("Outbound call initiated successfully");
    return res.data;
  } catch (error: any) {
    console.error("Error creating outbound call:", error);
    const message = error.response?.data?.message || "Failed to create outbound call";
    toast.error(message);
    throw error;
  }
}

