// Mute call (mute assistant)
export async function muteCall(callId: string, controlUrl: string) {
  try {
    const response = await fetch(controlUrl, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        type: 'control',
        control: 'mute-assistant'
      })
    });

    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    toast.success("Assistant muted");
    return await response.json();
  } catch (error: any) {
    console.error("Error muting call:", error);
    toast.error("Failed to mute call");
    throw error;
  }
}

