// End call via control URL - Fixed to use backend endpoint instead of direct VAPI call
export async function endCallViaControl(callId: string, controlUrl: string) {
  try {
    console.log('ðŸ›‘ Ending call via backend API:', { callId, controlUrl });

    // Use the backend endpoint and pass the control URL
    // This avoids CORS issues and properly handles authentication
    const response = await axiosInstance.post(`/api/outboundcall/endCall/${callId}`, {
      controlUrl: controlUrl // Pass control URL to backend for server-side call
    });

    if (response.data.success) {
      return response.data;
    } else {
      throw new Error(response.data.message || 'Failed to end call');
    }
  } catch (error: any) {
    console.error("Error ending call via control:", error);
    console.error("Error response data:", error.response?.data);
    console.error("Error response status:", error.response?.status);
    console.error("Error response headers:", error.response?.headers);

    // Log the detailed error structure
    if (error.response?.data) {
      console.error("Detailed error breakdown:");
      console.error("- success:", error.response.data.success);
      console.error("- message:", error.response.data.message);
      console.error("- message type:", typeof error.response.data.message);
      console.error("- message is array:", Array.isArray(error.response.data.message));
      console.error("- error object:", error.response.data.error);
      console.error("- debug info:", error.response.data.debug);
    }

    // Handle specific error cases
    let message = "Failed to end call";

    if (error.response?.status === 402) {
      message = "Insufficient credits to end call. Please purchase credits.";
    } else if (error.response?.status === 401) {
      message = "Authentication required. Please log in again.";
    } else if (error.response?.data?.message) {
      // Handle array messages
      if (Array.isArray(error.response.data.message)) {
        message = error.response.data.message.join(', ');
      } else {
        message = error.response.data.message;
      }
    } else if (error.message) {
      message = error.message;
    }

    throw error;
  }
}

